import React , {useState , useEffect }from 'react'
import Header from '../components/Header';
import Footsell from '../components/Footsell';
import { useDispatch, useSelector } from "react-redux";

import "./_ttuser.scss"
import { fetchUsers } from '../components/reduxuser';

function Ttuser() {

    const dispatch = useDispatch()
    useEffect(()=>{
     dispatch(fetchUsers())
    } , [])
    let listUser = useSelector((state) => state.quanliuser)
    console.log(listUser);
    return (  <>
      <Header hide="none"/>
  {listUser.status == "idle" ?      
      <div className="Ttuser">
        <div className="Ttuser__left">
            <div className="wrappar-img">
               
   
    <img src={listUser.listuser[Number(localStorage.getItem("ldsjfldlsf..."))].img} alt="" />
    
    
   
                 
                
            </div>
            <a href="" className="updateimg">Cp nht nh</a>

            <div className="xacnhan">
                <div className="daxacnhan">{listUser.listuser[Number(localStorage.getItem("ldsjfldlsf..."))].name}  xc nhn</div>
                <div className='check'>
                    <i className="fa-solid fa-check"></i>
                    <div>S in thoi</div>
                </div>
            </div>

        </div>
        <div className="Ttuser__right">
            <div className="right-top">Xin cho, ti l {listUser.listuser[Number(localStorage.getItem("ldsjfldlsf..."))].name}</div>
            <div className="thamgia">Bt u tham gia t nm 2023</div>
            <a href="">Chnh sa h s</a>
            <label htmlFor="">Gii thiu</label>
            <textarea name="" id="" cols="30" rows="5"></textarea>
            <label htmlFor="">V tr</label>
            <input type="text" />
            <label htmlFor="">Cng vic</label>
            <input type="text" />
            <div className='btn'>
                <a href="">Hy</a>
                <button>Lu</button>
            </div>
        </div>
        
        
      </div>
       : ""}
      <Footsell/>
    </>);
}

export default Ttuser;